@use '../abstracts' as *;

.row {
  max-width: $grid-width;
  margin: 0 auto;
  // background-color: #eee;

  @include respond(tab-port) {
    max-width: $grid-width-tab-port;
    padding: 0 3rem;
  }

  &:not(:last-child) {
    margin-bottom: $gutter-vartical;

    @include respond(tab-port) {
      margin-bottom: $gutter-vartical-small;
    }
  }

  @include clearfix;

  // Select all class start with (^), any class (*), any class end ($)
  [class^='col-'] {
    float: left;
    // background-color: brown;
    // color: $color-white;
    // text-align: center;
    // padding: 5px 0px;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;

      @include respond(tab-port) {
        margin-right: 0;
        margin-bottom: $gutter-vartical-small;
      }
    }

    @include respond(tab-port) {
      width: 100% !important;
    }
  }

  .col-1-of-2 {
    // # used for knowing variable
    width: calc((100% - #{$gutter-horizontal}) / 2);
  }

  .col-1-of-3 {
    width: calc((100% - 2 * #{$gutter-horizontal}) / 3);
  }

  .col-2-of-3 {
    width: calc(
      2 * ((100% - 2 * #{$gutter-horizontal}) / 3) + #{$gutter-horizontal}
    );
  }

  .col-1-of-4 {
    width: calc((100% - 3 * #{$gutter-horizontal}) / 4);
  }

  .col-2-of-4 {
    width: calc(
      2 * ((100% - 3 * #{$gutter-horizontal}) / 4) + #{$gutter-horizontal}
    );
  }

  .col-3-of-4 {
    width: calc(
      3 * ((100% - 3 * #{$gutter-horizontal}) / 4) + 2 * #{$gutter-horizontal}
    );
  }
}
